.burger-button {
    width: 50px;
    height: 50px;
    position: absolute;
    top: 10px;
    right: 15px;
    z-index: 5;
    line-height: 50px;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.2);
    color: white;
    border-radius: 50%;
    font-size: 1.5em;
  }
  
  .burger-button.dark {
    background: #0d47a1;
  }
  
  .header {
    position: sticky;
    width: 100%;
    height: 75px;
    background-color: #0d47a1;
  }

  .container {
    height: 75px;
    display: flex;
    flex-direction: row;
    align-items: center;
    /* border: 2px solid green; */
    width: 55%;
    margin-left: 22%;
  }
  
  .header figure {
    display: flex;
    align-items: center;
  }
  
  .header figure img {
    height: 2rem;
  }
  
  .menu ul {
    display: flex;
    align-items: center;
  }
  
  .menu ul li a {
    color: white;
    text-decoration: none;
    font-weight: 500;
    transition: color 0.3s ease;
    font-size: 1.2rem;
  }
  
  .menu ul li a:hover {
    color: lightgray;
  }
  
  @media screen and (min-width: 768px) {
    .burger-button {
      display: none;
    }
  
    .header .container {
      flex-direction: row;
      justify-content: space-between;
    }
  
    .header figure {
      margin: 0;
    }
  
    .header figure img {
      height: 2rem;
    }
  
    .header .menu {
      width: auto;
      height: 100%;
      position: initial;
      justify-content: end;
      background: none;
      font-size: 1rem;
    }
  
    .header .menu ul {
      height: inherit;
      font-size: 1.2em;
      flex-direction: row;
    }
  
    .header .menu ul li a {
      padding: 0 1em;
      height: inherit;
    }
  }
  